.log-out{
  text-align: center;
}

.log-out button{
  cursor: pointer;
  padding: 13px 15px;
  font-size: 10pt;
  font-weight: bold;
  border-radius: .3rem;
  border: transparent;
  background-color: var(--white1);
  margin-top: 10px;
}

.log-out button:hover{
  background-color: var(--red);
  color: var(--white1);
}