.predict-form{
  width: 95%;
  background-color: var(--white2);
  /* min-height: 300vh; */
  margin: auto auto;
  margin-bottom: 50px;
  border-radius: .5rem;
}

.predict-form .range{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px;
  background-color: var(--white1);
  padding: 5px;
  border-radius: .5rem;
}

.predict-form .range .sliderValue span{
  font-size: 18px;
  font-weight: bold;
  padding: 3px;
  border-radius: .5rem;
}

.predict-form .range h3{
  font-size: 15px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: var(--black1);
}

.predict-form .field{
  display: flex;
  align-items: center;
}

.predict-form .field input{
  width: 250px;
  height: 50px;
  cursor: pointer;
}

.predict-form .field input[type="radio"]{
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin: 5px;
}

.predict-form .data-submit{
  text-align: center;
}

.predict-form .data-submit input{
  padding: 10px 25px;
  border-radius: .5rem;
  cursor: pointer;
  font-size: large;
  font-weight: bold;
  width:200px;
  background-color: var(--primary);
  color: var(--white1);
  border: 1px solid transparent;
  transition: .1s;
}

.predict-form .data-submit input:hover{
  box-shadow: 2px 2px 4px var(--black);
}

.predict-form span{
  font-size: 12px;
}

.predict-home{
  height: 400px;
  background: #C04848;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #480048, #C04848);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #480048, #C04848); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.predict-home .bg-img{
  width: 200px;
  height: 200px;
  z-index: 2;
  background-image: url('../images/pngwing.png');
  background-size: 150px;
  background-position: center;
  background-repeat: no-repeat;
}

.predict-home a{
  text-decoration: none;
  color: var(--white1);
  background-color: var(--black1);
  padding: 10px;
  border-radius: .5rem;
  text-align: center;
  margin: 10px;
  font-size: 15px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
}

.predict-home a:hover{
  background-color: var(--black);
}

@media only screen and (max-width: 600px) {
  .predict-home{
    height: 200px;
  }
  .predict-home .bg-img{
    width: 100px;
    height: 100px;
    background-size: 100px;
  }
}