.navbar{
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--white);
  position: sticky;
  top: 0;
  z-index: 1;
}

.navbar>div *{
  text-decoration: none;
  font-size: 11pt;
  font-weight: bold;
  color: var(--black);
  padding: 2px;
  font-family: sans-serif;
}

.navbar .nav1{
  display: flex;
  justify-content: space-around;
  flex-basis: 250px;
  transition: .1s;
}

.navbar .nav1 *:hover{
  /* text-shadow: .3px .3px .5px var(--black); */
  color: var(--primary);
}

.navbar .nav2 *{
  font-size: 16pt;
  transition: .1s;
}

.navbar .nav2:hover *{
  color: var(--primary);
}

