.doctor-card{
  display: flex;
  background-color: var(--white);
  width: 95%;
  margin: 15px 5px;
  align-items: center;
  border-radius: .3rem;
  padding: 5px;
}
.doctor{
  font-size: small;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 200px;
  min-height: 100px;

}
.doctor img{
  width: 50px;
  border-radius: 5rem;
  margin: 5px;
}

