.footer{
  background-color:var(--black1);
  color: var(--white1);
  font-size: 14pt;
  border-top-left-radius: 3rem;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer .group-3{
  cursor: pointer;
  text-decoration: underline;
}

@media only screen and (max-width: 600px) {
  .footer{
    height: 100px;
    font-size: 13pt;
  }
}