.login{
  width: 100%;
  display: flex;
  justify-content: center;
}

.login form{
  width: 250px;
  padding: 5px;
  margin-top: 10px;
}

.login form div{
  display: flex;
  flex-direction: column;
}

input[type="text"], input[type="password"] {
  width: 100%;
  padding: 15px;
  margin: 5px 0 22px 0;
  display: inline-block;
  border: none;
  background: #f1f1f1;
  font-size: 13pt;
  border-radius: .3rem;
}

input[type="text"]:focus, input[type="password"]:focus {
  background-color: #ddd;
  outline: none;
  
}

input[type=submit]{
  background-color: var(--primary);
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
  font-size: 13pt;
  border-radius: .4rem;

}

.login .submit input:hover{
  opacity: 1;
}